@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.main-page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}

.top-section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: rgb(236, 236, 236);
}

.bottom-section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 3 1;
          flex: 3 1;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: rgb(255, 255, 255);
}

.bottom-section button {
  height: 20%;
  width: 20%;
  font-size: 30px;
}
.main-wrapper {
    display: -webkit-flex;
    display: flex;
    background: rgb(91, 91, 209);
    height: 100vh;
    width: 100vw;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.signiture {
    position: absolute;
    bottom: 0;
    left: 10px;
    color: rgb(177, 177, 241);
}

.box {
    display: -webkit-flex;
    display: flex;
    padding: 0px 40px;
    padding-top: 20px;
    border-radius: 20px;
    width: 40%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    background: -webkit-linear-gradient(top left, rgb(255, 255, 255), rgb(195, 195, 195));
    background: linear-gradient(to bottom right, rgb(255, 255, 255), rgb(195, 195, 195));
    box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.63);
}

.box-header {
    display: -webkit-flex;
    display: flex;
    font-size: 30px;
    margin-bottom: 40px;
}

.box-header > h1 {
    margin: 0;
    font-weight: 500;
}

.box-form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
}

.box-form input {
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    text-align: center;
    font-size: 20px;
    border-color: rgb(202, 202, 202);
    width: 100%;
    padding: 10px 0px;
    margin-bottom: 10px;
}


.box-form input:last-child {
    width: 100%;
    padding: 20px 0px;
}

.box-form button {
    padding: 20px 0px;
    width: 40%;
    margin: 0;
    margin-top: 20px;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}

.link {
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    margin-top: 30px;
    margin-bottom: 5px;
    width:100%;
}

.link-signup {
    font-weight: 500;
    color: rgb(91, 91, 209);
}
